<template>
    <div class="bottom-bar">
        <div class="check-content">
            <check-button
                    :is-checked="isSelectAll" class="check-button"
                    @click.native="checkClick"/>
            <span>全选</span>

        </div>
        <div class="price">
            合计：￥{{totalPrice}}
        </div>
        <div class="calclate">
            去计算{{checkLength}}
        </div>
    </div>
</template>

<script>
    import CheckButton from 'components/content/checkButton/CheckButton'
    import { mapGetters  } from 'vuex'
    export default {
        name: "CartBottomBar",
        components:{
            CheckButton
        },
        computed:{
            ...mapGetters(['cartList']),
            totalPrice(){
                return this.cartList.filter(item=>{
                    return item.checked
                }).reduce((preValue,item)=>{
                    return item.price * item.count+preValue
                },0).toFixed(2)
            },
            checkLength(){
                return this.cartList.filter(item => item.checked).length
            },
            isSelectAll(){
                if (this.cartList.length===0) return false
                // return !(this.cartList.filter(item => !item.checked).length)
                return !this.cartList.find(item => !item.checked)
                // 3.普通遍历
                // for (let item of this.cartList) {
                //     if (!item.checked){
                //         return  false
                //     }
                // }
                // return  true
            }
        },
        methods:{
            checkClick(){
                if (this.isSelectAll){//全部选中
                    this.cartList.forEach( item => item.checked=false)
                }else {
                    this.cartList.forEach( item => item.checked=true)
                }
            }
        }
    }
</script>

<style scoped>
    .bottom-bar{
        height: 40px;
        background-color: #eee;
        position: relative;
        line-height: 40px;
        display: flex;
    }
    .check-content{
        display: flex;
        align-items: center;
        margin-left: 10px;
        width: 70px;
    }
    .check-button{
        width: 20px;
        height: 20px;
        line-height: 20px;
        margin-right: 5px;
    }
    .price{
        margin-left: 10px;
        flex: 1;
    }
    .calclate{
        background: red;
        width: 80px;
        color: #fff;
        text-align: center;
    }
</style>